.loader {
    margin: 0 auto;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 3px dashed teal;
    animation: rotate 3s infinite linear;
}

@keyframes rotate {
    from {
        transform: rotate(0deg) scale(1);
    }
    to {
        transform: rotate(360deg) scale(1.4);
    }
}